import React from 'react'
import { Link } from 'react-router-dom'

import Logo from './global/Logo'

function NavBar() {
  const [isActive, setisActive] = React.useState(false);
  return (
    <nav className="navbar" role="navigation" aria-label="main navigation" style={{ borderBottom: '2px solid #E0E0E0' }}>
      <div className="container">
        <div className="navbar-brand">
          <Logo />

          <div onClick={() => {
            setisActive(!isActive);
          }}
            role="button" className={`navbar-burger burger ${isActive ? "is-active" : ""}`} aria-label="menu" aria-expanded="false" data-target="navbarBasicExample">
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
          </div>
        </div>

        <div id="navbarBasicExample" className={`navbar-menu ${isActive ? "is-active" : ""}`}>
          <div className="navbar-end" style={{ align: 'right' }}>
            <Link className='side_link' to='/job/abc'>
              Jobs
            </Link>

            <Link className='side_link' to='/post'>
              Post a Job
            </Link>
          </div>
        </div>
      </div>
    </nav>
  );
}

export default NavBar
